
import {mapMutations, mapState} from 'vuex'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import Scroll from '../scroll/scroll'

export default {
name: "question",
data() {
return {
question_list: [],
pulldown: true,
data_inte_detail: []
}
},
methods: {
back() {
this.$router.back();
this.back_show(false);
},
...mapMutations([
'back_show'
]),
show_question(data) {
this.data_inte_detail = [];
this.data_inte_detail.push(data);
console.log(this.data_inte_detail);
this.$router.push({
path: `/question/${data.id}`
})
},
delete_question(id) {
console.log(id);
this.$dialog.confirm({
title: '',
mes: '确定删除？',
opts: () => {
this.$http.post('/delete_question', {
id: id
}).then( response => {
if(response.body.flag == true) {
this.$dialog.toast({mes: '删除成功', timeout: 1000});
} else {
this.$dialog.toast({mes: '删除失败', timeout: 1000});
}
}, err => {
this.$dialog.toast({mes: '未知错误', timeout: 1000});
console.log(err);
})
this.$http.post('/question', {
user: this.user
}).then( response => {
this.question_list = response.body;
}, error => {
console.log(error);
})
}
});
},
update_question(value) {
this.$router.push({
path: '/Deliver',
query: {content: value.detail, title: value.title, id: value.id}
})
},
back_show_s() {
this.back_show(true);
}
},
created() {
this.$http.post('/question', {
user: this.user
}).then( response => {
this.question_list = response.body;

}, error => {
console.log(error);
})
},
computed: {
...mapState([
'user'
])
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
Scroll
}
}



