export default {
  name: "new_user",
  data() {
    return {
      cur_index: 1,
      btn_text: "下一步",
      user: "",
      password: "",
      password_again: "",
      phone: "",
      email: "",
      question: "",
      answer: ""
    };
  },
  methods: {
    next() {
      switch (this.cur_index) {
        case 1:
          if (this.password.length < 6) {
            this.$dialog.toast({
              mes: "密码长度不能小于6位！",
              timeout: 1500
            });
            return;
          } else if (this.password != this.password_again) {
            this.$dialog.toast({
              mes: "两次密码不相同！",
              timeout: 1500
            });
            return;
          } else {
            this.$http
              .post("/repeat_user", {
                user: this.user
              })
              .then(
                response => {
                  if (response.body.flag == false) {
                    this.$dialog.toast({
                      mes: "用户名已注册!",
                      timeout: 1500
                    });
                    return;
                  } else {
                    this.cur_index += 1;
                  }
                },
                err => {
                  console.log(err);
                }
              );
          }
          break;
        case 2:
          let email_re = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
          let phone_re = /^[1][3,4,5,7,8][0-9]{9}$/;
          if (phone_re.test(this.phone) == false) {
            this.$dialog.toast({
              mes: "手机号码格式错误！",
              timeout: 1500
            });
            return;
          } else if (email_re.test(this.email) == false) {
            this.$dialog.toast({
              mes: "邮箱格式错误！",
              timeout: 1500
            });
            return;
          } else {
            this.$http
              .post("/repeat_phone_email", {
                phone: this.phone,
                email: this.email
              })
              .then(
                response => {
                  if (response.body.flag == true) {
                    this.cur_index += 1;
                  } else {
                    this.$dialog.toast({
                      mes: "用户信息已使用！",
                      timeout: 1500
                    });
                    return;
                  }
                },
                err => {
                  console.log(err);
                }
              );
          }
          break;
        case 3:
          if (this.question.trim() == "") {
            this.$dialog.toast({
              mes: "密保问题不能为空！",
              timeout: 1500
            });
            return;
          } else if (this.answer.trim() == "") {
            this.$dialog.toast({
              mes: "答案不能为空！",
              timeout: 1500
            });
            return;
          } else {
            this.$http
              .post("/add_user", {
                user: this.user,
                password: this.password,
                phone: this.phone,
                email: this.email,
                question: this.question,
                answer: this.answer
              })
              .then(
                response => {
                  if (response.body.flag == true) {
                    this.cur_index += 1;
                  }
                },
                err => {
                  console.log(err);
                }
              );
          }
          break;
        case 4:
          this.$router.push({
            path: "/Login"
          });
          break;
      }
      // this.cur_index += 1;
    },
    forward() {
      this.cur_index -= 1;
    },
    go_login() {
      console.log("1");
      this.$router.push({
        path: "/Login"
      });
    }
  },
  watch: {
    cur_index() {
      this.btn_text = this.cur_index == 4 ? "返回登录" : "下一步";
    }
  }
};



