
import Scroll from "../../base/scroll/scroll";
import Wrapper from "../../base/wrapper/wrapper";
import { mapMutations, mapState } from "vuex";
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { setTimeout } from "timers";
export default {
name: "information",
data() {
return {
pulldown: true,
datalist: [],

i_data: [],
refresh: false
};
},
methods: {
show_iframe(data) {


this.i_data = [];
this.i_data.push(data);
this.show_flag = true;
this.back_show(true);

},
...mapMutations(["back_show", "set_head"]),
pulldown_s() {
this.refresh = true;
setTimeout(() => {
this.$http.get("/info").then(
response => {
this.datalist = response.data;
this.$dialog.toast({
mes: "刷新成功！",
timeout: 1500
});
this.refresh = false;
},
error => {
console.log("false");
}
);
}, 800);
}
},
components: {
Scroll,
Wrapper,
InlineLoading,
LoadMore
},
created() {
this.set_head("资讯");
this.back_show(false);
this.$http.get("/info").then(
response => {
this.datalist = response.data;
},
error => {
console.log("false");
}
);
},
filters: {
timeSubstr: function(value) {
return value.substr(0, 10);
}
},
computed: {
...mapState(["back_flag", "header_text"])
}
};



