
import { mapMutations, mapState } from "vuex";
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import Scroll from "../../base/scroll/scroll";
import { setTimeout } from "timers";

export default {
name: "interlocution",
data() {
return {
pulldown: true,
data_inte: [],
inte_flag: false,
data_inte_detail: [],
refresh: false
};
},
created() {
this.set_head("问答");
this.back_show(false);
this.$http.get("/inte").then(
response => {
this.data_inte = response.body;
this.data_inte.reverse();

},
error => {
console.log("false");
}
);
},
methods: {
...mapMutations(["back_show", "set_head"]),
set_detail(data) {
this.data_inte_detail = [];
this.data_inte_detail.push(data);

this.$router.push({
path: `/Interlocution/${data.id}`
});
this.back_show(true);
},
back_show_s() {
this.back_show(false);
this.$http.get("/inte").then(
response => {
this.data_inte = response.body;
this.data_inte.reverse();

},
error => {
console.log("false");
}
);
},
pulldown_s() {
this.refresh = true;
setTimeout(() => {
this.$http.get("/inte").then(
response => {
this.data_inte = response.body;
this.data_inte.reverse();
this.refresh = false;
this.$dialog.toast({
mes: "刷新成功！",
timeout: 1500
});
},
error => {
console.log("false");
}
);
}, 800);
}
},
computed: {
...mapState(["header_text", "user_img", "back_flag"])
},
components: {
Scroll,
InlineLoading,
LoadMore
}
};



