
import Scroll from "../../base/scroll/scroll";
import CourseWrapper from "../../base/course_wrapper/course_wrapper";
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapState, mapMutations } from "vuex";
import { setTimeout } from "timers";

export default {
components: {
Scroll,
CourseWrapper,
InlineLoading,
LoadMore
},
name: "course",
data() {
return {
data_course: [],
data_classify: [],
pulldown: true,
data_course_detail: {},
refresh: false
};
},
computed: {
...mapState(["back_flag", "header_text"])
},
created() {
this.set_head("教程");
this.back_show(false);
this.$http.get("/classify").then(
response => {
this.data_classify = response.data;
},
error => {
console.log("false");
}
);









},
methods: {
...mapMutations(["back_show", "set_head"]),
setDetail(data) {
this.data_course_detail = data;
this.$router.push({
path: `/Course/${data.name}`
});
this.back_show(true);
},
back_show_s() {

this.back_show(false);
},
pulldown_s() {
this.refresh = true;
setTimeout(() => {
this.$http.get("/classify").then(
response => {
this.data_classify = response.data;
this.refresh = false;
this.$dialog.toast({
mes: "刷新成功！",
timeout: 1500
});
},
error => {
console.log("false");
}
);
}, 800);
}
}
};



