import { mapMutations, mapState } from "vuex";

export default {
  name: "login",
  data() {
    return {
      name: "",
      password: "",
      pas_flag: true,
      from: null
    };
  },
  methods: {
    ...mapMutations(["back_show", "set_user", "set_login"]),
    clear(item) {
      if (item == 0) {
        this.name = "";
      } else if (item == 1) {
        this.password = "";
      }
    },
    show_pas() {
      this.pas_flag == false ? (this.pas_flag = true) : (this.pas_flag = false);
      this.$refs.password.type == "text"
        ? (this.$refs.password.type = "password")
        : (this.$refs.password.type = "text");
    },
    back() {
      this.$router.push({
        path: "Information"
      });
    },
    check_login() {
      if (this.name == "") {
        this.$dialog.toast({
          mes: "用户名不能为空！",
          timeout: 1500
        });
      } else if (this.password == "") {
        this.$dialog.toast({
          mes: "密码不能为空！",
          timeout: 1500
        });
      } else {
        this.$http
          .post("/login", {
            user: this.name,
            password: this.password
          })
          .then(
            response => {
              // console.log(response);
              if (response.body.length == 0) {
                this.$dialog.toast({
                  mes: "用户名或密码错误！",
                  timeout: 1500
                });
              } else if (response.body[0].login == true) {
                this.set_user({
                  user: response.body[0].user,
                  password: response.body[0].password,
                  img: response.body[0].img
                });
                this.set_login({
                  login_flag: response.body[0].login,
                  is_admin: response.body[0].admin
                });
                this.$router.push({
                  path: "/"
                });
              }
            },
            error => {
              console.log("false");
            }
          );
      }
    },
    re_password() {
      this.$router.replace({
        path: "/Password"
      });
      this.back_show(true);
    },
    add_user() {
      this.$router.replace({
        path: "/NewUser"
      });
      this.back_show(true);
    }
  },
  created() {
    this.back_show(true);
  },
  computed: {
    ...mapState(["user", "user_password", "user_img"])
  }
};



