import {mapMutations, mapState} from 'vuex'

export default {
  name: "setting",
  data() {
    return {
      switch1: false
    }
  },
  methods: {
    back() {
      this.$router.back();
      this.back_show(false);
    },
    ...mapMutations([
      'back_show',
      'set_autoUpdate'
    ])
  },
  computed: {
    ...mapState([
      'auto_update'
    ])
  },
  watch: {
    switch1() {
      this.set_autoUpdate(this.switch1);
    }
  },
  created() {
    this.switch1 = this.auto_update;
  }
}



