import Scroll from "../scroll/scroll";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    Scroll
  },
  name: "inte_wrapper",
  data() {
    return {
      pulldown: true,
      comment: null,
      tab: 0,
      l_flag: false,
      reply_user: null,
      comment_list: [],
      reply_detail: null,
      popup_flag: false,
      comment_id: null
    };
  },
  props: {
    data_inte_detail: Array
  },
  methods: {
    back() {
      this.$router.back();
      this.$emit("back_show");
      // this.back_show(false);
    },
    ...mapMutations(["back_show"]),
    send_comment() {
      if (this.comment == null) {
        this.$dialog.toast({
          mes: "内容不能为空！",
          timeout: 1500
        });
      } else {
        this.$http
          .post("comment", {
            id: this.data_inte_detail[0].id,
            commenter: this.user,
            time: new Date().toLocaleDateString(),
            detail: this.comment,
            user_img: this.user_img,
            reply_user: this.reply_user == null ? null : this.reply_user,
            reply_detail: this.reply_detail == null ? null : this.reply_detail
          })
          .then(
            response => {
              if (response.body.flag == true) {
                this.$dialog.toast({
                  mes: "回复成功！",
                  timeout: 1500
                });
                this.comment = null;
                this.popup_flag = false;
                this.$http.get("/inte/" + this.data_inte_detail[0].id).then(
                  response => {
                    this.comment_list = response.body;
                  },
                  error => {
                    console.log("false");
                  }
                );
              }
            },
            error => {
              console.log("false");
            }
          );
      }
    },
    set_reply(user, detail, c_id) {
      if (user == this.user) {
        this.$dialog.toast({
          mes: "不能回复自己！",
          timeout: 1500
        });
      } else {
        this.reply_user = user;
        this.reply_detail = detail;
        this.popup_flag = true;
        // this.tab = 1;
      }
      this.comment_id = c_id;
    }
  },
  computed: {
    ...mapState(["login_flag", "user", "user_img"])
  },
  mounted() {
    // console.log(this.data_inte_detail);
    if (this.login_flag) {
      setTimeout(() => {
        this.$refs.content.style.height =
          this.$refs.content.offsetHeight > this.$refs.contentp.$el.offsetHeight
            ? this.$refs.content.offsetHeight
            : this.$refs.contentp.$el.offsetHeight + 5 + "px";
      }, 100);
    }
  },
  created() {
    if (this.login_flag == false) {
      this.$router.replace({
        path: "/Login"
      });
    }
    this.$http.get("/inte/" + this.data_inte_detail[0].id).then(
      response => {
        this.comment_list = response.body;
      },
      error => {
        console.log("false");
      }
    );
  },
  watch: {
    tab() {
      this.comment = null;
      this.reply_detail = null;
    },
    comment_id() {
      this.comment = null;
    }
  }
};



