import { mapMutations, mapState } from "vuex";
import Scroll from "../scroll/scroll";

export default {
  name: "person",
  data() {
    return {
      pulldown: false,
      data_person: [],
      isReadOnly: true,
      btn: "编辑",
      age: null,
      sex: null,
      email: null,
      phone: null
    };
  },
  methods: {
    back() {
      this.$router.back();
      this.back_show(false);
    },
    ...mapMutations(["back_show"]),
    set_readonly() {
      if (this.btn == "确定") {
        this.$dialog.confirm({
          title: "",
          mes: "修改后的手机号码和邮箱将作为您的联系方式，是否确定修改？",
          opts: [
            {
              txt: "否",
              color: false,
              callback: () => {
                this.isReadOnly = false;
                this.btn = "确定";
                this.set_readonly();
              }
            },
            {
              txt: "是",
              color: true,
              callback: () => {
                let email_re = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
                let phone_re = /^[1][3,4,5,7,8][0-9]{9}$/;
                if (email_re.test(this.email) == false) {
                  this.$dialog.toast({
                    mes: "邮箱格式错误！",
                    timeout: 1500
                  });
                } else if (phone_re.test(this.phone) == false) {
                  this.$dialog.toast({
                    mes: "手机号码格式错误！",
                    timeout: 1500
                  });
                } else if (this.age == "") {
                  this.$dialog.toast({
                    mes: "年龄不能为空！",
                    timeout: 1500
                  });
                } else {
                  this.$http
                    .post("/personUpdate", {
                      user: this.user,
                      age: this.age,
                      sex: this.sex,
                      email: this.email,
                      phone: this.phone
                    })
                    .then(
                      response => {
                        if (response.body.flag == true) {
                          this.$dialog.toast({
                            mes: "修改成功！",
                            timeout: 1500
                          });
                          this.isReadOnly = true;
                          this.btn = "编辑";
                        }
                      },
                      error => {
                        console.log(error);
                      }
                    );
                }
              }
            }
          ]
        });
      } else {
        this.isReadOnly = !this.isReadOnly;
        this.btn = this.isReadOnly == true ? "编辑" : "确定";
      }
    }
  },
  components: {
    Scroll
  },
  computed: {
    ...mapState(["user"])
  },
  created() {
    this.$http
      .post("person", {
        user: this.user
      })
      .then(
        response => {
          this.data_person = response.body;
          this.age = response.body[0].age;
          this.sex = response.body[0].sex;
          this.email = response.body[0].email;
          this.phone = response.body[0].phone;
        },
        error => {
          console.log("false");
        }
      );
  }
};



