
import { mapState, mapMutations } from "vuex";
import Scroll from "../../base/scroll/scroll";
import InlineLoading from 'vux/src/components/inline-loading/index.vue'


export default {
name: "course_wrapper",
data() {
return {
data_course_detail: {},
popup_flag: false,
pulldown: true,
detail_flag: false,
detail_title: null,
detail: null,
collection_flag: false
};
},
props: {
data_course: Object
},
computed: {
...mapState(["back_flag", "user"])
},
methods: {
...mapMutations(["back_show"]),
back() {
this.$emit("back_show");
this.$router.back();
},
show_detail(num) {

if (num == undefined) {
return;
} else {
this.detail_flag = true;
this.popup_flag = false;
this.$http.get("/detail?catanum=" + num).then(
response => {

this.detail = response.data;
this.detail_title = this.detail[0].catalog;

},
error => {
console.log(error);
}
);
}
},
detail_back() {
this.detail_flag = false;
},
set_collection() {
if (this.user == null) {
this.$dialog.toast({
mes: "请先登录！",
timeout: 1500
});
} else {
if (this.collection_flag == true) {
this.$http
.post("/delete_collection", {
name: this.data_course.name,
user: this.user
})
.then(
response => {
if (response.body.flag == true) {
this.$dialog.toast({
mes: "取消收藏成功！",
timeout: 1500
});
} else {
this.$dialog.toast({
mes: "取消收藏失败！",
timeout: 1500
});
}
this.collection_flag = !response.body.flag;
},
err => {
this.$dialog.toast({
mes: "未知错误！",
timeout: 1500
});
console.log(err);
}
);
} else if (this.collection_flag == false) {
this.$http
.post("/add_collection", {
name: this.data_course.name,
user: this.user
})
.then(
response => {
if (response.body.flag == true) {
this.$dialog.toast({
mes: "收藏成功！",
timeout: 1500
});
} else {
this.$dialog.toast({
mes: "收藏失败！",
timeout: 1500
});
}
this.collection_flag = response.body.flag;
},
err => {
this.$dialog.toast({
mes: "未知错误！",
timeout: 1500
});
console.log(err);
}
);
}
}
}
},
created() {
this.$http.get("/classify/" + this.data_course.name).then(
response => {
this.data_course_detail = response.data;
},
error => {
console.log("false");
}
);
this.$http
.post("/select_collection", {
user: this.user,
name: this.data_course.name
})
.then(
response => {
this.collection_flag = response.body.flag;
},
err => {
console.log(err);
}
);
},
components: {
Scroll,
InlineLoading
}
};



