import {mapState, mapMutations} from 'vuex'
import Scroll from "../scroll/scroll";

export default {
  components: {Scroll},
  name: "wrapper",
  data() {
    return {
      pulldown: true
    }
  },
  computed: {
    ...mapState([
      'back_flag'
    ])
  },
  methods: {
    ...mapMutations([
      'back_show'
    ])
  },
  props: {
    data: Array
  },
  created() {
    // console.log(this.data)
  },
  filters: {
    timeSubstr: function (value) {
      return value.substr(0, 10)
    }
  }
}



