import { mapMutations, mapState } from "vuex";
import Scroll from "../../base/scroll/scroll";
import FileUpload from "vue-simple-upload/dist/FileUpload";

export default {
  components: {
    Scroll
  },
  name: "mine",
  data() {
    return {
      pulldown: true
    };
  },
  created() {
    this.set_head("个人中心");
    this.back_show(false);
  },
  methods: {
    ...mapMutations([
      "back_show",
      "set_head",
      "set_user",
      "set_login",
      "set_userImg"
    ]),
    logout() {
      this.set_user({
        user: null,
        password: null,
        img: null
      });
      this.set_login({
        login_flag: false,
        is_admin: false
      });
      this.$router.push({
        path: "/login"
      });
    },
    show_info(index, flag) {
      if (flag == false) {
        this.back_show(true);
        this.$router.push({
          path: `${index}`
        });
      } else {
        if (this.login_flag) {
          this.back_show(true);
          this.$router.push({
            path: `${index}`
          });
        } else {
          this.$dialog.toast({
            mes: "您尚未登录！",
            timeout: 1500
          });
        }
      }
    },
    finishUpload(e) {
      var resobj = JSON.parse(e.currentTarget.response);
      let reg = /^(\s|\S)+(jpg|png|jpeg|heic|JPG|PNG|JPEG|HEIC)+$/;
      let flag = reg.test(resobj.file[0].originalFilename);
      console.log(resobj);
      if (flag == true) {
        this.$http
          .post("/user_img_update", {
            imgPath: resobj.file[0].path,
            user: this.user
          })
          .then(
            response => {
              if (response.body.flag == true) {
                this.$dialog.toast({
                  mes: "头像修改成功！",
                  timeout: 1500
                });
                this.set_userImg(resobj.file[0].path);
              } else {
                this.$dialog.toast({
                  mes: "修改失败！",
                  timeout: 1500
                });
              }
            },
            err => {
              console.log(err);
            }
          );
      } else {
        this.$dialog.toast({
          mes: "请选择图片上传（仅支持png/jpg/heic）！",
          timeout: 1500
        });
      }
    }
  },
  computed: {
    ...mapState(["login_flag", "header_text", "user_img", "user"])
  },
  components: {
    fileupload: FileUpload
  }
};



