
import {mapMutations, mapState} from 'vuex'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import Scroll from '../scroll/scroll'

export default {
name: "connection",
data() {
return {
collection_list: [],
pulldown: true,
collection_detail: {},
collection_flag: true
}
},
methods: {
back() {
this.$router.back();
this.back_show(false);
},
...mapMutations([
'back_show'
]),
set_detail(name) {
this.$http.post('/collection_course', {
name: name
}).then(response => {
this.collection_flag = false;
this.collection_detail = response.body[0];
this.$router.push({
path: `/Collection/${name}`
})
}, err => {
console.log(err);
})
},
back_show_s() {
this.back_show(true);
this.collection_flag = true;
},
delete_collection(name, user) {
this.$dialog.confirm({
title: '',
mes: '确定取消收藏？',
opts: () => {
this.$http.post('/delete_collection', {
name: name,
user: user
}).then(response => {
if(response.body.flag == true) {
this.$dialog.toast({mes: '取消收藏成功', timeout: 1000});
} else {
this.$dialog.toast({mes: '取消收藏失败', timeout: 1000});
}
this.$http.post('/collection', {
user: this.user
}).then(response => {
this.collection_list = response.body;
}, err => {
console.log(err);
})
}, err => {
this.$dialog.toast({mes: '未知错误', timeout: 1000});
})
}
});
}
},
created() {
this.$http.post('/collection', {
user: this.user
}).then(response => {
this.collection_list = response.body;

}, err => {
console.log(err);
})
},
computed: {
...mapState([
'user'
])
},
components: {
Swipeout,
SwipeoutItem,
SwipeoutButton,
Scroll
}
}



