export default {
  name: "password",
  data() {
    return {
      cur_index: 1,
      btn_text: '下一步',
      phone: '',
      question: '',
      answer: '',
      new_password: '',
      p_answer: '',
      password: ''
    }
  },
  methods: {
    next() {
      switch (this.cur_index) {
        case 1:
          let phone_re = /^[1][3,4,5,7,8][0-9]{9}$/;
          if (phone_re.test(this.phone) == false) {
            this.$dialog.toast({
              mes: '手机号码格式错误！',
              timeout: 1500
            });
            return ;
          } else {
            this.$http.post('/password', {
              phone: this.phone
            }).then( response => {
              console.log(response);
              this.question = response.body[0].question;
              this.p_answer = response.body[0].answer;
              this.password = response.body[0].password;
            }, err => {
              console.log(err);
            })
          }
          break ;
        case 2:
          if(this.answer.trim() != this.p_answer) {
            this.$dialog.toast({
              mes: '答案错误！',
              timeout: 1500
            });
            return ;
          }
          break;
        case 3:
          if(this.new_password.trim() == '') {
            this.$dialog.toast({
              mes: '密码不能为空！',
              timeout: 1500
            });
            return ;
          } else if(this.new_password.length < 6){
            this.$dialog.toast({
              mes: '密码长度不能小于6！',
              timeout: 1500
            });
            return ;
          } else if(this.new_password == this.password) {
            this.$dialog.toast({
              mes: '新密码与旧密码相同！',
              timeout: 1500
            });
            return ;
          } else {
            this.$http.post('/new_password', {
              password: this.new_password,
              phone: this.phone
            }).then(response => {
              console.log(response);
            }, err=> {
              console.log(err);
            })
          }
          break;
        case 4:
          this.$router.replace({
            path: '/Login'
          })
      }
      this.cur_index += 1;
    },
    forward() {
      this.cur_index -= 1;
    },
    go_login() {
      this.$router.push({
        path: '/Login'
      });
    }
  },
  watch: {
    cur_index() {
      this.btn_text = this.cur_index == 4 ? '返回登录' : '下一步';
    }
  }
}



